export default {
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [
				{
					state: 0,
					text: '全部'
				},
				{
					state: 1,
					text: '待付款'
				},
				{
					state: 2,
					text: '待确认'
				},
				{
					state: 3,
					text: '待审核'
				},
				{
					state: 4,
					text: '历史订单'
				}
			],
            buy:'所属矿池',
            state:'待付款',
            price:'购买单价',
            numbr:'购买数量',
            pay:'支付金额',
            trading:'交易类型',
            dater:'下单日期',
            shod:false
		};
	},
    onShow() {
        
    },
	methods: {
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that=this;
			that.tabCurrentIndex = index;
		}
	}
};
